[class^="att-"]:before, [class*=" att-"]:before {
  font-family: "loft";
  font-style: normal;
  font-weight: normal;
  speak: never;

  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */

  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;

  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
 /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;

  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */

  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}

.att-phone:before { content: '\e800'; } /* '' */
.att-instagram:before { content: '\f16d'; } /* '' */
.att-vkontakte:before { content: '\f189'; } /* '' */
.att-whatsapp:before { content: '\f232'; } /* '' */
.att-telegram:before { content: '\f2c6'; } /* '' */
/* FONTELLO END*/

@font-face {
	font-display: swap;
	font-family: 'Oswald';
	src: url("./Oswald/Oswald-VariableFont_wght.ttf");
}

body {
  margin: 0;
  font-family: "Oswald", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.font-14 {
  font-size: 14px;
}