.app-header {
    /* background-color: #282c34; */
    /* background: url("https://static.wixstatic.com/media/e2f9fe_6e4f08416e36478989b3649d67a650d4~mv2_d_5333_3993_s_4_2.jpg/v1/fill/w_2880,h_1530,al_c,q_90,usm_0.66_1.00_0.01/e2f9fe_6e4f08416e36478989b3649d67a650d4~mv2_d_5333_3993_s_4_2.webp"); */
    background-position: 50% 70%;
    min-height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    color: white;
    position: fixed;
    width: 100%;
    top: 0;
}

.app-header__opacity {
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.4;
    background-color: #282c34;
}

.app-header__menu {
    width: 100%;
    height: 20vh;
    position: fixed;
    top: 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

ul {
    list-style: none;
}

ul li {
    display: inline-block;
    padding: 0 10px;
}

ul li a {
    color: #fff;
    text-decoration: none;
    padding: 5px 10px;
}

.link--item_selected::after {
    content: '';
    width: 100%;
    height: 2px;
    background: #fff;
}